body {
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch__hand {
  transform-origin: bottom;
  position: absolute;
  bottom: 50%;
  left: 50%;
}

.seconds-hand {
  z-index: -1;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate-second-hand;
  position: absolute;
  left: 49%;
  transform: translate(-50%, -100%)rotate(360deg);
}

.minutes-hand {
  z-index: -1;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate-minute-hand;
  position: absolute;
  left: 48%;
}

.center {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes rotate-minute-hand {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

@keyframes rotate-second-hand {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.stopwatch--speed-up .seconds-hand {
  animation-duration: 10s;
}

.stopwatch--speed-up .minutes-hand {
  animation-duration: 600s;
}
